.pagination {
  width: 60%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 auto;
  padding: 20px 0;
  .page-link {
    list-style-type: none;
    width: 100%;
    border: 2px solid rgb(83, 80, 80);
    color: white;
    text-align: center;
    font-size: 20px;
    border-radius: 5px;
    padding: 10px;
    text-decoration: none;
  }
}
